@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap);
* {
    box-sizing: border-box;
    font-family: 'Rubik', sans-serif;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-wrapper {
    background: linear-gradient(90deg, rgba(31,29,30,1) 0%, rgba(127,144,0,1) 100%);
    box-shadow: 0 0px 20px 5px #000;
    position: relative;
}
.header-links {
    height: 50px;
    display: flex;
    align-items: flex-start;
    position: absolute;
    top: 70px;
}
.header-link {
    text-transform: uppercase;
    letter-spacing: 4px;
    color: rgba(225, 255, 0, 0.493);
    background: rgba(31,29,30,1);
    text-decoration: none;
    font-size: 18px;
    padding: 5px 20px;
    display: flex;
    align-items: center;
    transition: .3s;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
}
.header-link-active {
    color: rgb(225, 255, 0);
}
.header-logo {
    display: flex;
    align-items: center;
    font-size: 34px;
    font-weight: 600;
    letter-spacing: 2px;
    color: #bac43a;
    text-shadow: 0 0 5px #000;
}
.header-logo img {
    width: 50px;
    margin: 10px 40px;
}
.entry {
    display: grid;
    grid-template-columns: 100px 1fr 100px 100px 55px;
    padding: 10px 30px;
    border-radius: 10px;
    background: linear-gradient(90deg, rgb(180, 147, 163) 0%, rgb(199, 175, 202) 100%);
    margin-bottom: 4px;
    align-items: center;
}
.entry-actions span {
    cursor: pointer;
    display: flex;
    justify-content: flex-end;
}
.entry-actions svg path {
    fill: rgb(3, 2, 29);
    transition: .4s;
}
.entry-actions span:hover svg path {
    fill: crimson;
}
.entry-date {
    font-size: 14px;
}
.entry-status span {
    padding: 1px 5px;
    border-radius: 4px;
    cursor: pointer;
}
.list {
    background: #a5ae177a;
    padding: 10px;
    border-radius: 10px;
    height: calc(100vh - 160px);
    max-width: 800px;
    margin: 0 auto;
    overflow: hidden;
}
.list-header {
    display: grid;
    grid-template-columns: 100px 1fr 100px 100px 55px;
    padding: 10px 35px 10px 30px;
    font-size: 13px;
    text-transform: uppercase;
    letter-spacing: 1px;
}
.list-content {
    overflow-y: scroll;
    align-self: center;
    padding-right: 5px;
    height: 80%;
}
.list-content::-webkit-scrollbar{
    width: 3px;
    background: rgb(196, 129, 22);
}
.list-content::-webkit-scrollbar-thumb {
    background: rgb(211, 170, 162);
}
.list-add {
    background: linear-gradient(90deg, rgba(180, 147, 163, 0.486) 0%, rgb(199, 175, 202, 0.486) 100%);
    margin-right: 9px;
    padding: 4px 20px 4px 30px;
    margin-bottom: 5px;
    border-radius: 10px;
    display: flex;
    align-items: center;
}
.list-btn {
    border-radius: 40%;
    width: 30px;
    height: 30px;
    font-size: 24px;
    font-weight: 900;
    border: none;
    background: none;
    color: rgb(3, 2, 29);
    cursor: pointer;
    margin-left: auto;
}
.list-add select, input {
    outline: none;
    background: none;
    border: 1px solid #000;
    border-radius: 3px;
    padding: 1px 3px;
}
.list-add input {
    flex-grow: 1;
    margin: 0 60px;
}
.page {
    z-index: 0;
    height: calc(100vh - 100px);
    padding: 50px 20px 30px;
    transition: .5s;
    overflow: hidden;
}
.page::after {
    position: fixed;
    z-index: 10;
    content: 'Copyright © Serhii Lisniakov';
    font-size: 10px;
    width: calc(100vw + 20px);
    height: 30px;
    background: linear-gradient(270deg, rgba(31,29,30,1) 0%, rgba(127,144,0,1) 100%);
    bottom: 0px;
    left: -20px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.page-list {
    background: rgba(4, 51, 38, 0.8);
}
.page-entries {
    background: rgba(65, 34, 37, 0.8);
}
.page-error {
    background: rgba(0, 0, 0, 0.8);
    color: #aa9494;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-family: monospace;
}
